<template>
  <div class="bigbox">
    <!-- <mrc_ess_table :headers='headers'
                   v-if="$route.name=='internationIndex'"
                   hideAddBtn
                   hideFooter
                   :slotKey="['client_type','versionArr']"
                   :paginationOptions='options'
                   @turnPage='getlist'
                   :isLoading='loading'
                   :items='desserts'> -->
    <mrc_ess_table :headers='headers'
                   v-if="$route.name=='internationIndex'"
                   hideAddBtn
                   hideFooter
                   :slotKey="['client_type','versionArr']"
                   :isLoading='loading'
                   :items='desserts'>
      <template #item.client_type='{item}'>
        <v-btn rounded
               style="opacity:0.8;"
               color="#ffffff"
               dark
               @click="detailClient(item)"
               small>
          <!-- <mrc_ess_svgIcon v-if="item.client_type_label!='Android手机App'&&item.client_type_label!='IOS手机App'"
                           :iconClass="item.client_type_label"
                           style="font-size: 23px;" />
          <mrc_ess_svgIcon v-if="item.client_type_label=='Android手机App'"
                           iconClass="Android"
                           style="font-size: 23px;" />
          <mrc_ess_svgIcon v-if="item.client_type_label=='IOS手机App'"
                           iconClass="IOS"
                           style="font-size: 23px;" /> -->
          <mrc_ess_svgIcon :iconClass="item.client_type"
                           style="font-size: 23px;" />
          <span class="black--text ml-2">
            {{item.client_type_label}}
          </span>
        </v-btn>
      </template>
      <template v-slot:item.versionArr='{item}'>
        <span v-for="(item1,index) in item.version"
              :key="index"
              @click="detailVersion(item,index)"
              class="hoverspan version">
          {{item1}}{{index==item.version.length-1?'':','}}
        </span>
      </template>

      <!-- 按钮 -->
      <template v-slot:item.actions='{item}'>
        <mrc_ess_addBtn v-btnAuth="['add-label-version']"
                        @handleAdd="handleAdd(item)"></mrc_ess_addBtn>
      </template>
    </mrc_ess_table>
    <router-view v-else></router-view>
  </div>
</template>

<script>
import { getLabelList } from '@/service/api/internationalization'
export default {
  components: {},
  props: {},
  data () {
    return {
      loading: false,
      desserts: [],
      // options: {
      //   pagesize: 5,//条数
      //   cpage: this.$store.state.cpage,  //页数
      //   totalCount: null, //总条数
      //   totalpage: null, //总页数
      // },

    };
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  created () { },
  async mounted () {
    await this.getlist()
  },
  methods: {
    // rowgodetail (item) {
    //   // console.log(item);
    //   this.$store.commit('set_iconstatus', true)  //行内进图标显示
    //   this.$store.commit('set_uuid', item.uuid)  //
    //   this.$store.commit('set_subPage_name', item.language_master_key)  //
    //   this.$router.push({
    //     name: 'detailverifymessagemanagement',
    //     params: { title: item.language_master_key },
    //   })
    // },
    // handleClick ({ index }, item) {
    //   switch (index) {
    //     case 0:
    //       this.$store.commit('set_iconstatus', false)  //行内进图标显示
    //       this.$store.commit('set_uuid', item.uuid)  //
    //       this.$store.commit('set_subPage_name', item.language_master_key)  //
    //       this.$router.push({
    //         name: 'detailverifymessagemanagement',
    //         params: { title: item.language_master_key }
    //       })
    //       break
    //     case 1:
    //       this.$myConfirm({
    //         content: '确认要删除吗？'
    //       }).then(res => {
    //         deletehint({
    //           uuid: item.uuid
    //         }).then(res => {
    //           if (res.code == 200) {
    //             this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
    //             this.getlist()
    //           }
    //         })
    //       }).catch(err => { })
    //       break
    //   }
    // },
    detailClient (item) {
      this.$store.commit('set_version', '')  //版本号
      this.$store.commit('set_client_type', item.client_type)  //客户端类型
      this.$router.push({
        name: 'allClientMsg'
      })
    },
    detailVersion (item, index) {
      this.$store.commit('set_version', item.version[index])  //版本号
      this.$store.commit('set_client_type', item.client_type)  //客户端类型
      this.$router.push({
        name: 'allClientMsg'
      })
    },
    handleAdd (item) {
      this.$store.commit('set_version', '')  //版本号
      this.$store.commit('set_client_type', item.client_type)  //客户端类型
      this.$router.push({
        name: 'addAllClientMsg'
      })
    },
    async getlist (obj) {
      this.loading = true
      if (this.desserts.length == 1 && this.options.cpage != 1) this.options.cpage--
      // if (obj) {
      //   const { cpage, pagesize } = obj
      //   this.options = obj
      // }
      const res = await getLabelList(
        // {
        //   cpage: this.options.cpage,
        //   pagesize: this.options.pagesize
        // }
      )
      if (res.code === '200') {
        res.data.map(item => {
          item.version = item.version.indexOf(',') != -1 ? item.version.split(',') : [item.version]
          item.version.sort((a, b) => {
            return b - a
          })
        })
        this.desserts = res.data
        // console.log(this.desserts);
        // this.options.totalCount = res.totalCount
        // this.options.totalpage = res.totalpage
      }
      this.loading = false
    },
  },
  computed: {
    headers () {
      return [
        {
          text: this.$t('label_Application_platform'),
          align: 'start',
          sortable: false,
          value: 'client_type',
        },
        {
          text: this.$t('label_version'),
          align: 'start',
          sortable: false,
          value: 'versionArr',
        },
        {
          text: '',
          value: 'actions',
          sortable: false,
          align: 'end',
        }
      ]
    },
    menulist () {
      return [
        { name: this.$t('label_Edit'), show: true },
        { name: this.$t('label_delete'), show: true },
      ]
    },
  },
  watch: {
    $route (v) {
      if (v.name == 'internationIndex') {
        this.getlist()
      }
    }
  },
}
</script>

<style scoped>
.bigbox /deep/.addBtn {
  position: static;
  top: 0px;
  right: 0px;
}
.hoverspan:hover {
  color: #0f46c1;
  font-size: 16px;
  cursor: pointer;
}
.version {
  font-size: 16px;
}
</style>
